@import 'color';
@import 'font';
@import 'variable';

body {
  background-color: $white;
  color: $cgrey;
  font-family: $default;

  img{
    display: block;
  }
}

#infobox{
  background-image: url("https://www.visitcopenhagen.com/sites/default/files/asp/visitcopenhagen/Corporate/TUT2018/oksnehallen_1700x600.png");
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 30% 50%;
  color: $white;
  height: 500px;
  padding-left: 15px;
  padding-top: 5%;
  a{
    color: $orange;
    text-decoration: none;
  }
}

.grid1 p{
  background-color: $orange;
  padding: 1%;
  font-size: 110%;
}

header {
  margin: $marginh;
  background-image: url("../img/png/ubuntu_white-orange.png");
  background-color: $orange;
  padding: $fifteen;
  height: 100px;
  background-repeat: no-repeat;
  background-size: contain;

  h2{
    float: right;
    margin-left: 15px;
  }
  a{
    color: $white;
    text-decoration: none;
    font-size: 120%;
  }
}

article{
  border: $border;
  border-radius: $borderr;
  margin: $margin;
}

#tilmed{
  background-color: lighten($wgrey, 30%);
  padding: 15px;
  h1{
    text-align: center;
  }
}

.gridsimple{
  display: grid;
  grid-template-columns: 50% 50%;
}

.nyheder{
  background-color: lighten($wgrey, 30%);
  color: $tgrey;
  display: grid;
  grid-template-columns: 15% 15% 15% 15%;
  grid-column-gap: 5%;
  padding: 1% 2%;

  h3{
    color: $orange;
    text-align:center;
  }
}

#links{
  background-color: $aub;
  color: $white;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-column-gap: 0%;
  text-align: center;
  padding: 15px;
  img{
    display: inline-block;
    width: 20%;
    height: 20%;
  }
}

footer {
  background-color: $orange;
  padding: $fifteen;
  color: $white;
  width: 97%;
  margin: auto;
}
